         <div class=\"form-group\">\r\n                    <label class=\"col-sm-4 control-label\">型号：</label>\r\n                    <div class=\"col-sm-8\">\r\n                        <input type=\"text\" class=\"form-control\" v-model=\"param.model\" placeholder=\"模糊\">\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </form>\r\n        <div class=\"text-center btn-operate\">\r\n            <button type=\"button\" class=\"btn btn-default\" @click=\"refresh\">\r\n                查询\r\n            </button>\r\n            <button v-if=\"perm.新增交换机 || perm.all\" type=\"button\" class=\"btn btn-default\" @click=\"$broadcast('showCreate')\">\r\n                新增交换机\r\n            </button>\r\n            <button v-if=\"perm.批量修改交换机 || perm.all\" type=\"button\" class=\"btn btn-default\" @click=\"batchEdit\">\r\n                批量修改\r\n            </button>\r\n            <dropdown v-el:confirm v-if=\"perm.删除交换机 || perm.all\">\r\n                <button type=\"button\" class=\"btn btn-default\" data-toggle=\"dropdown\">\r\n                    批量删除\r\n                    <span class=\"caret\"></span>\r\n                </button>\r\n                <div slot=\"dropdown-menu\" class=\"dropdown-menu pd20\">\r\n                    <button type=\"button\" class=\"btn btn-danger btn-block\" @click=\"deleteFn\">确定</button>\r\n                    <button type=\"button\" class=\"btn btn-default btn-block\" @click=\"cancelFn\">取消</button>\r\n                </div>\r\n            </dropdown>\r\n            <button v-if=\"perm.导出交换机 || perm.all\" type=\"button\" class=\"btn btn-default\" @click=\"exportFn\">\r\n                导出\r\n            </button>\r\n        </div>\r\n        <div class=\"text-center table-title\">\r\n            查询结果\r\n            <div class=\"pull-left\">\r\n                <dropdown>\r\n                    <button type=\"button\" class=\"btn btn-default set-btn\" data-toggle=\"dropdown\">\r\n                      